.headerTextContainer {
  z-index: 1;
  position: relative;
  background-color: #fff;
  padding: 20px 15%;
  border-bottom: solid 2px #98e2c6ff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-family: 'Nunito', sans-serif; }
  .headerTextContainer a {
    text-decoration: none;
    color: #265e42; }
  .headerTextContainer p a:hover {
    text-decoration: underline; }
  .headerTextContainer h1 {
    padding: 0 6px 2px 6px;
    font-size: 1.5rem;
    -webkit-transition: background-size .4s ease;
    transition: background-size .4s ease;
    text-transform: uppercase;
    background: -webkit-linear-gradient(top, transparent 62%, #b9f5d8ff 0) center center/0% 75% no-repeat;
    background: linear-gradient(to bottom, transparent 62%, #b9f5d8ff 0) center center/0% 75% no-repeat;
    cursor: pointer;
    color: #265e42;
    letter-spacing: .08rem; }
    .headerTextContainer h1:hover {
      background-size: 100% 100%; }
    .headerTextContainer h1:active {
      background-size: 80% 100%; }

