@import "../fonts-colors.scss";

.headerTextContainer {
  z-index: 1;
  position: relative;
  background-color: #fff;
  padding: 20px 15%;
  border-bottom: solid 2px $mediumGreenColor;
  // box-shadow: 0 0 90px 10px rgba(95, 124, 179, 0.15);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-family: 'Nunito', sans-serif;
  a {
    text-decoration: none;
    color: $greenTextColor;
    }
  p a:hover{
    text-decoration: underline;
  }
  h1{
    padding: 0 6px 2px 6px;
    font-size: 1.5rem;
    transition: background-size .4s ease;
    text-transform: uppercase;
    background: linear-gradient(to bottom, transparent 62%, $lightGreenColor 0) center center/0% 75% no-repeat;
    cursor: pointer;
      color: $greenTextColor;
      letter-spacing: .08rem;
    &:hover {
      background-size: 100% 100%;
    }
    &:active {
      background-size: 80% 100%;
    }
  }

}
